<!--
  tabsoptionsoptionscustomClass
-->

<template>
  <div>
    <el-form-item :label="i18nt('designer.setting.customClass')">
      <el-select v-model="optionModel.customClass" multiple filterable allow-create default-first-option>
        <el-option v-for="(item, idx) in cssClassList" :key="idx" :label="item" :value="item"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item :label="i18nt('designer.setting.tabPaneSetting')"></el-form-item>
    <el-form-item label-width="0" class="panes-setting">
      <draggable tag="ul" :list="selectedWidget.tabs" item-key="id" v-bind="{ group: 'panesGroup', ghostClass: 'ghost', handle: '.drag-option' }">
        <template #item="{ element: tpItem, index: tpIdx }">
          <li class="col-item">
            <!-- span style="margin-right: 12px">{{tpIdx + 1}}</span -->
            <el-checkbox v-model="tpItem.options.active" disabled @change="evt => onTabPaneActiveChange(evt, tpItem)" style="margin-right: 8px">{{ i18nt('designer.setting.paneActive') }}</el-checkbox>
            <el-input type="text" v-model="tpItem.options.label" style="width: 155px"></el-input>
            <i class="iconfont icon-drag drag-option"></i>
            <el-button circle plain size="small" type="danger" @click="deleteTabPane(selectedWidget, tpIdx)" icon="el-icon-minus" class="col-delete-button"></el-button>
          </li>
        </template>
      </draggable>
      <div>
        <el-button type="primary" link @click="addTabPane(selectedWidget)">{{ i18nt('designer.setting.addTabPane') }}</el-button>
      </div>
    </el-form-item>
  </div>
</template>

<script>
import { reactive, toRefs, getCurrentInstance } from 'vue';
import { useI18n } from '@/utils/i18n';
// import Draggable from 'vuedraggable'
import { deepClone } from '@/utils/util';

export default {
  componentName: 'PropertyEditor',
  components: {
    // Draggable,
  },
  name: 'm-tab-customClass-editor',
  props: {
    designer: Object,
    optionModel: Object,
    selectedWidget: Object,
  },
  setup(props) {
    const { i18nt } = useI18n();
    const { proxy } = getCurrentInstance();

    const data = reactive({
      cssClassList: [],
    });

    data.cssClassList = deepClone(props.designer.getCssClassList());
    // css
    props.designer.handleEvent('form-css-updated', cssClassList => {
      data.cssClassList = cssClassList;
    });

    const onTabPaneActiveChange = (evt, tpItem) => {
      // TODO: !!!
    };

    const addTabPane = curTabs => {
      props.designer.addTabPaneOfTabs(curTabs);
      props.designer.emitHistoryChange();
    };

    const deleteTabPane = (curTabs, tpIdx) => {
      if (curTabs.tabs.length === 1) {
        proxy.$message.info(i18nt('designer.hint.lastPaneCannotBeDeleted'));
        return;
      }

      props.designer.deleteTabPaneOfTabs(curTabs, tpIdx);
      props.designer.emitHistoryChange();
    };

    return {
      i18nt,
      ...toRefs(props),
      ...toRefs(data),

      addTabPane,
      deleteTabPane,
      onTabPaneActiveChange,
    };
  },
};
</script>

