<template>
  <static-content-wrapper
    :designer="designer"
    :field="field"
    :design-state="designState"
    :display-style="field.options.displayStyle"
    :parent-widget="parentWidget"
    :parent-list="parentList"
    :index-of-parent-list="indexOfParentList"
    :sub-form-row-index="subFormRowIndex"
    :sub-form-col-index="subFormColIndex"
    :sub-form-row-id="subFormRowId"
  >
    <van-button
      ref="fieldEditor"
      :type="field.options.type"
      :size="size"
      :plain="field.options.plain"
      :round="field.options.round"
      :icon="field.options.icon"
      :disabled="fieldDisabled"
      native-type="button"
      @click="handleButtonWidgetClick"
      >{{ field.options.label }}</van-button
    >
    <!-- chromemask -->
    <div v-if="designState" class="design-mask"></div>
  </static-content-wrapper>
</template>

<script>
import { toRefs, getCurrentInstance, reactive, onMounted, onBeforeUnmount, nextTick } from 'vue';
import { useI18n } from '@/utils/i18n';
import StaticContentWrapper from './static-content-wrapper';
import { useField } from '@/components/form-designer/form-widget/field-widget/fieldMixin';
export default {
  componentName: 'FieldWidget',
  components: {
    StaticContentWrapper,
  },
  name: 'm-button-widget',
  props: {
    designState: {
      default: false,
      type: Boolean,
    },
    designer: Object,
    field: Object,
    indexOfParentList: Number,
    parentList: Array,

    parentWidget: Object,

    subFormColIndex: {
      default: -1,
      /* 0 */
      type: Number,
    },
    subFormRowId: {
      default: '',
      /* Idid */
      type: String,
    },
    subFormRowIndex: {
      default: -1,
      /* 0 */
      type: Number,
    },
  },
  setup() {
    const { i18nt } = useI18n();
    const fieldMixin = useField(reactive({}));

    onMounted(() => {
      fieldMixin.handleOnMounted();
    });

    onBeforeUnmount(() => {
      fieldMixin.unregisterFromRefList();
    });

    fieldMixin.registerToRefList();
    fieldMixin.initEventHandler();
    fieldMixin.handleOnCreated();

    return {
      i18nt,
      ...fieldMixin,
    };
  },
};
</script>

<style lang="scss" scoped>
@import '../../../../styles/global.scss'; //* static-content-wrapper *//

.design-mask {
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
}
</style>
