    <el-form-item label-width="0" class="panes-setting">
      <draggable :list="selectedWidget.widgetList" item-key="id" v-bind="{ group: 'panesGroup', ghostClass: 'ghost', handle: '.drag-option' }" @update="dragUpdate">
        <template #item="{ element: tpItem, index: tpIdx }">
