              <li :key="tpIdx" class="col-item">
                <el-radio v-model="selectedWidget.options.expandName" :label="tpItem.id" :value="tpItem.id" style="margin-right: 8px">{{ i18nt('extension.setting.collapseItemExpand') }}</el-radio>
                <el-input type="text" v-model="tpItem.options.title" style="width: 125px"></el-input>
                <i class="iconfont icon-drag drag-option"></i>
                <el-button circle plain size="small" type="danger" @click="itemPropertiesDialogShow(tpItem)" icon="el-icon-more" class="col-delete-button"></el-button>
                <el-button circle plain size="small" type="danger" @click="deleteCollapseItem(selectedWidget, tpIdx)" icon="el-icon-minus" class="col-delete-button"></el-button>
              </li>
            </template>
            <!-- Checkbox -->
            <template v-else>
              <el-checkbox-group v-model="selectedWidget.options.expandName" :key="tpIdx">
                <el-checkbox :label="tpItem.options.name" :value="tpItem.options.name" style="margin-right: 2px"
                  >{{ i18nt('extension.setting.collapseItemExpand') }}

                  <el-input type="text" v-model="tpItem.options.title" style="width: 125px"></el-input>
                  <i class="iconfont icon-drag drag-option"></i>
                  <el-button circle plain size="small" type="danger" @click="itemPropertiesDialogShow(tpItem)" icon="el-icon-more" class="col-delete-button"></el-button>
                  <el-button circle plain size="small" type="danger" @click="deleteCollapseItem(selectedWidget, tpIdx)" icon="el-icon-minus" class="col-delete-button"></el-button>
                </el-checkbox>
              </el-checkbox-group>
            </template>
          </transition-group>
        </template>
      </draggable>

      <div>
        <el-button type="primary" link @click="addCollapseItem(selectedWidget)">{{ i18nt('designer.setting.addTabPane') }}</el-button>
