    </el-form-item>

    <el-dialog :title="i18nt('extension.setting.collapseItemProperyDialogTitle')" v-model="dialogVisible" width="400px" :before-close="itemPropertiesDialogClose">
      <el-form :model="dialogCurrentItemSetting" size="small" label-position="left" label-width="120px" class="setting-form" @submit.prevent>
        <el-form-item :label="i18nt('extension.setting.collapseItemTitle')">
