        <el-form-item :label="i18nt('extension.setting.collapseItemIcon')">
          <el-input type="text" v-model="dialogCurrentItemSetting.icon" style="width: 125px"></el-input>
        </el-form-item>
        <el-form-item :label="i18nt('extension.setting.collapseItemSize')">
          <el-select v-model="dialogCurrentItemSetting.size">
            <el-option label="default" value=""></el-option>
            <el-option label="large" value="large"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item :label="i18nt('extension.setting.collapseItemIsLink')">
          <el-switch v-model="dialogCurrentItemSetting['is-link']"></el-switch>
