        <el-form-item :label="i18nt('extension.setting.collapseItemLazyRender')">
          <el-switch v-model="dialogCurrentItemSetting['lazy-render']"></el-switch>
        </el-form-item>
        <el-form-item :label="i18nt('extension.setting.collapseItemTitleClass')">
          <el-select v-model="dialogCurrentItemSetting['title-class']" multiple filterable allow-create default-first-option>
            <el-option v-for="(item, idx) in cssClassList" :key="idx" :label="item" :value="item"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item :label="i18nt('extension.setting.collapseItemValueClass')">
          <el-select v-model="dialogCurrentItemSetting['value-class']" multiple filterable allow-create default-first-option>
            <el-option v-for="(item, idx) in cssClassList" :key="idx" :label="item" :value="item"></el-option>
          </el-select>
