<script>
export default {
  componentName: 'PropertyEditor',
  data() {
    return {
      cssClassList: [],
      dialogCurrentItemSetting: {},
      dialogVisible: false,
      expandName: '',
    };
  },
  name: 'collapse-accordion-editor',
};
</script>
<script setup>
import { watch, getCurrentInstance, nextTick } from 'vue';

import { useI18n } from '@/utils/i18n';
import { deepClone, generateId } from '@/utils/util';

const { proxy } = getCurrentInstance();
const props = defineProps({
  designer: Object,
  optionModel: Object,
  selectedWidget: Object,
});

const { i18nt } = useI18n();

nextTick(() => {
  proxy.cssClassList = deepClone(props.designer.getCssClassList());
  //css
  props.designer.handleEvent('form-css-updated', cssClassList => {
    proxy.cssClassList = cssClassList;
  });
});

watch(
  () => props.selectedWidget.options.accordion,
  val => {
    //
    if (val) {
      props.selectedWidget.options.expandName = '';
    } else {
      props.selectedWidget.options.expandName = [];
    }
  }
);

const onActiveChange = (evt, tpItem) => {
  //TODO: !!!
};

const addCollapseItem = curTabs => {
  const collapseItems = curTabs.widgetList;
  let itemPane = deepClone(props.designer.getContainerByType('collapse-item'));
  itemPane.id = 'collapse-item-' + generateId();
  itemPane.options.name = itemPane.id;
  itemPane.options.label = 'collapse-item-' + (collapseItems.length + 1);
  collapseItems.push(itemPane);

  props.designer.emitHistoryChange();
};

const deleteCollapseItem = (curTabs, tpIdx) => {
  if (curTabs.widgetList.length === 1) {
    proxy.$message.info(this.i18nt('designer.hint.lastPaneCannotBeDeleted'));
    return;
  }

  curTabs.widgetList.splice(tpIdx, 1);
  props.designer.emitHistoryChange();
};

const itemPropertiesDialogShow = collaseItem => {
  proxy.dialogVisible = true;
  proxy.dialogCurrentItemSetting = collaseItem.options;
};

const itemPropertiesDialogClose = () => {
  proxy.dialogVisible = false;
};
const itemPropertiesDialogConfirm = () => {
  proxy.dialogVisible = false;
};

const dragUpdate = () => {};
</script>

