<template>
  <static-content-wrapper
    :designer="designer"
    :field="field"
    :design-state="designState"
    :parent-widget="parentWidget"
    :parent-list="parentList"
    :index-of-parent-list="indexOfParentList"
    :sub-form-row-index="subFormRowIndex"
    :sub-form-col-index="subFormColIndex"
    :sub-form-row-id="subFormRowId"
  >
    <div ref="fieldEditor" :style="!!field.options.fontSize ? `font-size: ${px2rem(field.options.fontSize)};` : ''">
      {{ field.options.textContent }}
    </div>
  </static-content-wrapper>
</template>

<script>
import { reactive, toRefs, onMounted, onBeforeUnmount } from 'vue';
import StaticContentWrapper from './static-content-wrapper';
import { useI18n } from '@/utils/i18n';
import { useField } from '@/components/form-designer/form-widget/field-widget/fieldMixin';
import { px2rem } from '@/utils/util';

export default {
  componentName: 'FieldWidget', //FieldWidgetbroadcast
  components: {
    StaticContentWrapper,
  },
  name: 'm-static-text-widget',
  props: {
    designState: {
      default: false,
      type: Boolean,
    },
    designer: Object,
    field: Object,
    indexOfParentList: Number,
    parentList: Array,

    parentWidget: Object,

    subFormColIndex: {
      default: -1,
      /* 0 */
      type: Number,
    },
    subFormRowId: {
      default: '',
      /* Idid */
      type: String,
    },
    subFormRowIndex: {
      default: -1,
      /* 0 */
      type: Number,
    },
  },
  setup(props) {
    const { i18nt } = useI18n();

    const data = reactive({
      px2rem: px2rem,
    });
    const fieldMixin = useField(data);

    onMounted(() => {
      fieldMixin.handleOnMounted();
    });

    onBeforeUnmount(() => {
      fieldMixin.unregisterFromRefList();
    });

    fieldMixin.registerToRefList();
    fieldMixin.initEventHandler();
    fieldMixin.handleOnCreated();

    return {
      i18nt,
      ...toRefs(props),
      ...toRefs(data),
      ...fieldMixin,
    };
  },
};
</script>

<style lang="scss" scoped>
@import '../../../../styles/global.scss'; //* static-content-wrapper *//
</style>
