<template>
  <el-container class="main-container full-height">
    <el-header class="main-header" v-if="vfLogoHeader">
      <div class="float-left main-title">
        <img src="../../assets/vform-logo.png" @click="openHome" />
        <span class="bold">{{ vfProductName }}</span> {{ vfProductTitle }} <span class="version-span">Ver {{ vFormVersion }}</span>
      </div>
      <div class="float-right external-link">
        <el-dropdown v-if="showLink('languageMenu')" :hide-timeout="2000" @command="handleLanguageChanged">
          <span class="el-dropdown-link">{{ curLangName }}<svg-icon icon-class="el-arrow-down" /></span>
          <template #dropdown>
            <el-dropdown-menu>
              <el-dropdown-item command="zh-CN">{{ i18nt('application.zh-CN') }}</el-dropdown-item>
              <el-dropdown-item command="en-US">{{ i18nt('application.en-US') }}</el-dropdown-item>
            </el-dropdown-menu>
          </template>
        </el-dropdown>

        <a v-if="showLink('externalLink')" href="javascript:void(0)" @click="ev => openUrl(ev, gitUrl)" target="_blank"><svg-icon icon-class="github" />{{ i18nt('application.github') }}</a>
        <a v-if="showLink('externalLink')" href="javascript:void(0)" @click="ev => openUrl(ev, docUrl)" target="_blank"><svg-icon icon-class="document" />{{ i18nt('application.document') }}</a>
        <a v-if="showLink('externalLink')" href="javascript:void(0)" @click="ev => openUrl(ev, chatUrl)" target="_blank">{{ i18nt('application.qqGroup') }}</a>
        <a v-if="showLink('externalLink')" href="javascript:void(0)" @click="ev => openUrl(ev, subScribeUrl)" target="_blank">
          {{ i18nt('application.subscription') }}<i class="el-icon-top-right"></i
        ></a>

        <a href="javascript:void(0)">&nbsp;</a>
      </div>
    </el-header>

    <el-container style="overflow: hidden">
      <el-aside class="side-panel" :style="{ width: leftAsideVisible ? toolBoxWidth : '0px' }">
        <div class="side-panel-wrapper" :style="{ width: toolBoxWidth }">
          <widget-panel :designer="designer" v-if="!toolBoxStyle" />
          <widget-panel-left :designer="designer" v-if="toolBoxStyle" />
        </div>
        <div class="left-aside-toggle-bar" @click="toggleLeftAside">
          <ArrowLeftBold class="icon" style="width: 12px" v-if="leftAsideVisible"></ArrowLeftBold>
          <ArrowRightBold class="icon" style="width: 12px" v-else></ArrowRightBold>
        </div>
      </el-aside>

      <el-container class="center-layout-container">
        <el-header class="toolbar-header">
          <toolbar-panel :designer="designer" :global-dsv="globalDsv" ref="toolbarRef">
            <template v-for="(idx, slotName) in $slots" #[slotName]>
              <slot :name="slotName"></slot>
            </template>
          </toolbar-panel>
        </el-header>
        <el-main class="form-widget-main">
          <div class="container-scroll-bar" style="height: 100%">
            <v-form-widget :designer="designer" :form-config="designer.formConfig" :global-dsv="globalDsv" ref="formRef">
              <template v-for="(idx, slotName) in $slots" #[slotName]="scope">
                <slot :name="slotName" v-bind="scope"></slot>
              </template>
            </v-form-widget>
          </div>
        </el-main>
      </el-container>

      <el-aside class="setting-panel" :style="{ width: rightAsideVisible ? '300px' : '0px' }">
        <div class="side-panel-wrapper">
          <setting-panel :designer="designer" :selected-widget="designer.selectedWidget" :form-config="designer.formConfig" :global-dsv="globalDsv" />
        </div>
        <div class="right-aside-toggle-bar" @click="toggleRightAside">
          <ArrowRightBold style="width: 12px" v-if="rightAsideVisible" />
          <ArrowLeftBold style="width: 12px" v-else />
        </div>
      </el-aside>
    </el-container>
  </el-container>
</template>

<script>
import { computed, getCurrentInstance, reactive, ref, nextTick, toRefs, provide, onMounted } from 'vue';

import WidgetPanel from './widget-panel/index';
import WidgetPanelLeft from './widget-panel/index-left';
import ToolbarPanel from './toolbar-panel/index';
import SettingPanel from './setting-panel/index';
import VFormWidget from './form-widget/index';
import { addWindowResizeHandler, deepClone, getQueryParam, getAllContainerWidgets, getAllFieldWidgets, traverseAllWidgets } from '@/utils/util';
import { MOCK_CASE_URL, VARIANT_FORM_VERSION } from '@/utils/config';
import { useI18n, useChangeLocale } from '@/utils/i18n';

import { createDesigner } from '@/components/form-designer/designer';
import SvgIcon from '@/components/svg-icon/index';
import { ArrowLeftBold, ArrowRightBold } from '@element-plus/icons-vue';
export default {
  components: {
    ArrowLeftBold,
    ArrowRightBold,
    SettingPanel,
    SvgIcon,
    ToolbarPanel,
    VFormWidget,
    WidgetPanel,
    WidgetPanelLeft,
  },
  name: 'VmFormDesigner',
  props: {
    /*  */
    bannedWidgets: {
      default: () => [],
      type: Array,
    },

    /*  */
    designerConfig: {
      default: () => {
        return {
          clearDesignerButton: true, // 
          eventCollapse: true, // 
          exportCodeButton: true, // 
          exportJsonButton: true, // JSON
          externalLink: true, // GitHub

          formTemplates: true, // 
          importJsonButton: true, // JSON
          languageMenu: true, // 
          languageName: 'zh-CN', // 
          logoHeader: true, // LogoPro
          presetCssCode: '', // CSS

          previewFormButton: true, // 
          productName: '', // VForm ProPro

          productTitle: '', // Pro
          resetFormJson: false, // 

          toolBoxStyle: 'left', //lefttop
          toolbarMaxWidth: 420, // 

          toolbarMinWidth: 300, // 
          widgetNameReadonly: false, // 
        };
      },
      type: Object,
    },
    /* API */
    fieldListApi: {
      default: null,
      type: Object,
    },
    /*  */
    globalDsv: {
      default: () => ({}),
      type: Object,
    },
  },
  setup(props, context) {
    const { i18nt } = useI18n();
    const { proxy } = getCurrentInstance();
    let $current = proxy;

    /*  */
    const toolbarRef = ref(null);
    const formRef = ref(null);

    /*  */
    onMounted(() => {
      initLocale();

      loadCase();
      loadFieldListFromServer();
    });

    const data = reactive({
      chatUrl: 'https://vform666.com/pages/chat-group/',
      curLangName: ref(''),
      curLocale: '',

      designer: createDesigner(proxy),
      docUrl: 'https://www.yuque.com/vform-mobile/doc/',
      externalComponents: {}, // ,
      fieldList: [],

      gitUrl: 'https://gitee.com/vdpadmin/vform-mobile-builds',

      leftAsideVisible: true,

      rightAsideVisible: true,

      scrollerHeight: 0,
      subScribeUrl: 'https://vform666.com/pages/mobile-pro/',

      vFormVersion: VARIANT_FORM_VERSION,
      vformInfo: proxy.$vformInfo,
    });

    provide('serverFieldList', data.fieldList);
    provide('getBannedWidgets', () => {
      return props.bannedWidgets;
    });
    provide('getDesignerConfig', () => {
      return props.designerConfig;
    });

    /*  */
    data.vsCodeFlag = getQueryParam('vscode') === 1;
    data.caseName = getQueryParam('case');

    const vfProductName = computed(() => {
      if (import.meta.env.VITE_APP_ENV_STANDARD) {
        return 'VForm Mobile';
      }

      return (props.designerConfig && props.designerConfig.productName) || (data.vformInfo && data.vformInfo.productName) || 'VForm Mobile';
    });
    const vfProductTitle = computed(() => {
      if (import.meta.env.VITE_APP_ENV_STANDARD) {
        return i18nt('application.productTitle');
      }
      return (props.designerConfig && props.designerConfig.productTitle) || (data.vformInfo && data.vformInfo.productTitle) || i18nt('application.productTitle');
    });

    const vfLogoHeader = computed(() => {
      if (import.meta.env.VITE_APP_ENV_STANDARD) {
        return true;
      }

      if (props.designerConfig.logoHeader === undefined) return true;
      else return props.designerConfig.logoHeader;
    });

    const toolBoxStyle = computed(() => {
      return props.designerConfig.toolBoxStyle === 'left';
    });

    const toolBoxWidth = computed(() => {
      return toolBoxStyle.value ? '312px' : '260px';
    });

    const testEEH = (eventName, eventParams) => {};

    const showLink = configName => {
      if (props.designerConfig[configName] === undefined) {
        return true;
      }

      return !!props.designerConfig[configName];
    };

    /*  */
    const initLocale = () => {
      data.curLocale = localStorage.getItem('vm_form_locale');
      if (data.vsCodeFlag) {
        data.curLocale = data.curLocale || 'en-US';
      } else {
        data.curLocale = data.curLocale || 'zh-CN';
      }

      data.curLangName = i18nt('application.' + data.curLocale);
      changeLanguage(data.curLocale);
    };
    const changeLanguage = langName => {
      useChangeLocale(langName);
    };
    const handleLanguageChanged = command => {
      changeLanguage(command);
      data.curLangName = i18nt('application.' + command);
    };

    /*  */
    const loadCase = () => {
      if (!data.caseName) {
        return;
      }

      axios
        .get(MOCK_CASE_URL + data.caseName + '.txt')
        .then(res => {
          if (res.data.code) {
            $current.$message.error(i18nt('designer.hint.sampleLoadedFail'));
            return;
          }

          setFormJson(res.data);
          $current.$message.success(i18nt('designer.hint.sampleLoadedSuccess'));
        })
        .catch(error => {
          $current.$message.error(i18nt('designer.hint.sampleLoadedFail') + ':' + error);
        });
    };
    const loadFieldListFromServer = () => {
      if (!props.fieldListApi) {
        return;
      }

      let headers = props.fieldListApi.headers || {};
      axios
        .get(props.fieldListApi.URL, { headers })
        .then(res => {
          let labelKey = props.fieldListApi.labelKey || 'label';
          let nameKey = props.fieldListApi.nameKey || 'name';

          data.fieldList.splice(0); // 
          res.data.forEach(fieldItem => {
            data.fieldList.push({
              label: fieldItem[labelKey],
              name: fieldItem[nameKey],
            });
          });
        })
        .catch(error => {
          $current.$message.error(error);
        });
    };

    /*  */
    const openHome = () => {
      if (data.vsCodeFlag) {
        const msgObj = {
          cmd: 'openUrl',
          data: {
            url: 'https://www.vform666.com/',
          },
        };
        window.parent.postMessage(msgObj, '*');
      }
    };
    const openUrl = (event, url) => {
      if (data.vsCodeFlag) {
        const msgObj = {
          cmd: 'openUrl',
          data: {
            url,
          },
        };
        window.parent.postMessage(msgObj, '*');
      } else {
        let aDom = event.currentTarget;
        aDom.href = url;
        // window.open(url, '_blank') //
      }
    };

    /*  */

    const setFormJson = formJson => {
      let modifiedFlag = false;
      if (formJson) {
        if (typeof formJson === 'string') {
          modifiedFlag = data.designer.loadFormJson(JSON.parse(formJson));
        } else if (formJson.constructor === Object) {
          modifiedFlag = data.designer.loadFormJson(formJson);
        }

        if (modifiedFlag) {
          data.designer.emitHistoryChange();
        }
      }
    };

    const getFormJson = () => {
      return {
        formConfig: deepClone(data.designer.formConfig),
        widgetList: deepClone(data.designer.widgetList),
      };
    };

    const clearDesigner = () => {
      toolbarRef.value.clearFormWidget();
    };

    /**
     * 
     */
    const refreshDesigner = () => {
      // this.designer.loadFormJson( this.getFormJson() )  //
      let fJson = getFormJson();
      data.designer.clearDesigner(true); // 
      data.designer.loadFormJson(fJson);
    };

    /**
     * 
     */
    const previewForm = () => {
      toolbarRef.value.previewForm();
    };

    /**
     * JSON
     */
    const importJson = () => {
      toolbarRef.value.importJson();
    };

    /**
     * JSON
     */
    const exportJson = () => {
      toolbarRef.value.exportJson();
    };

    /**
     * Vue/HTML
     */
    const exportCode = () => {
      toolbarRef.value.exportCode();
    };

    /**
     * 
     * @param widgetList nulljson
     * @param staticWidgetsIncluded 
     * @returns {*[]}
     */
    const getFieldWidgets = (widgetList = null, staticWidgetsIncluded = false) => {
      return widgetList ? getAllFieldWidgets(widgetList, staticWidgetsIncluded) : getAllFieldWidgets(data.designer.widgetList, staticWidgetsIncluded);
    };

    /**
     * 
     * @param widgetList nulljson
     * @returns {*[]}
     */
    const getContainerWidgets = (widgetList = null) => {
      return widgetList ? getAllContainerWidgets(widgetList) : getAllContainerWidgets(data.designer.widgetList);
    };

    /**
     * json
     * @param formJson
     */
    const upgradeFormJson = formJson => {
      if (!formJson.widgetList || !formJson.formConfig) {
        data.$message.error('Invalid form json!');
        return;
      }

      traverseAllWidgets(formJson.widgetList, w => {
        data.designer.upgradeWidgetConfig(w);
      });
      data.designer.upgradeFormConfig(formJson.formConfig);

      return formJson;
    };

    const getWidgetRef = (widgetName, showError = false) => {
      return formRef.value.getWidgetRef(widgetName, showError);
    };

    const getSelectedWidgetRef = () => {
      return formRef.value.getSelectedWidgetRef();
    };

    /**
     * 
     * @param dsObj
     */
    const addDataSource = dsObj => {
      data.designer.formConfig.dataSources.push(dsObj);
    };

    /**
     * getEC()VForm
     * @param componentName 
     * @param externalComponent 
     */
    const addEC = (componentName, externalComponent) => {
      data.externalComponents[componentName] = externalComponent;
    };

    /**
     * 
     * @param componentName 
     * @returns {boolean}
     */
    const hasEC = componentName => {
      return data.externalComponents.hasOwnProperty(componentName);
    };

    /**
     * 
     * @param componentName
     * @returns {*}
     */
    const getEC = componentName => {
      return data.externalComponents[componentName];
    };

    const toggleLeftAside = () => {
      data.leftAsideVisible = !data.leftAsideVisible;
    };

    const toggleRightAside = () => {
      data.rightAsideVisible = !data.rightAsideVisible;
    };

    // 
    data.designer.initDesigner(!!data.designer.resetFormJson);
    data.designer.loadFormContentFromStorage();

    return {
      formRef,
      /*  */
      toolbarRef,

      /*  */
      ...toRefs(data),
      ...props,
      addDataSource,
      addEC,

      /*  */
      changeLanguage,
      clearDesigner,
      exportCode,
      exportJson,
      getContainerWidgets,
      getEC,
      getFieldWidgets,
      getFormJson,

      getSelectedWidgetRef,
      getWidgetRef,
      handleLanguageChanged,
      hasEC,

      /*  */
      i18nt,
      importJson,
      openHome,
      openUrl,
      previewForm,
      refreshDesigner,
      setFormJson,
      showLink,
      testEEH,
      toggleLeftAside,
      toggleRightAside,
      toolBoxStyle,
      toolBoxWidth,
      upgradeFormJson,
      vfLogoHeader,

      /*  */
      vfProductName,
      vfProductTitle,
    };
  },
};
</script>
