<template>
  <div class="toolbar-container">
    <div class="left-toolbar">
      <el-button type="primary" link :disabled="undoDisabled" size="small" :title="i18nt('designer.toolbar.undoHint')" @click="undoHistory"> <svg-icon icon-class="undo" /></el-button>
      <el-button type="primary" link :disabled="redoDisabled" size="small" :title="i18nt('designer.toolbar.redoHint')" @click="redoHistory"> <svg-icon icon-class="redo" /></el-button>
      <el-button-group style="margin-left: 20px">
        <!-- <el-button :type="layoutType === 'PC' ? 'info' : ''" @click="changeLayoutType('PC')"> {{ i18nt('designer.toolbar.pcLayout') }}</el-button> -->
        <el-button :type="layoutType === 'Pad' ? 'info' : ''" @click="changeLayoutType('Pad')"> {{ i18nt('designer.toolbar.padLayout') }}</el-button>
        <el-button :type="layoutType === 'H5' ? 'info' : ''" @click="changeLayoutType('H5')"> {{ i18nt('designer.toolbar.mobileLayout') }}</el-button>
      </el-button-group>
      <el-button style="margin-left: 20px" :title="i18nt('designer.toolbar.nodeTreeHint')" @click="showNodeTreeDrawer"> <svg-icon icon-class="node-tree" /></el-button>
    </div>

    <el-drawer
      :title="i18nt('designer.toolbar.nodeTreeTitle')"
      direction="ltr"
      v-model="showNodeTreeDrawerFlag"
      :modal="true"
      :size="280"
      append-to-body
      :destroy-on-close="true"
      class="node-tree-drawer"
    >
      <el-tree ref="nodeTree" :data="nodeTreeData" node-key="id" default-expand-all highlight-current class="node-tree" icon-class="el-icon-arrow-right" @node-click="onNodeTreeClick"></el-tree>
    </el-drawer>

    <div class="right-toolbar">
      <div class="right-toolbar-con">
        <el-button v-if="showToolButton('clearDesignerButton')" type="primary" link @click="clearFormWidget"> <svg-icon icon-class="el-delete" />{{ i18nt('designer.toolbar.clear') }}</el-button>
        <el-button v-if="showToolButton('previewFormButton')" type="primary" link @click="previewForm"> <svg-icon icon-class="el-view" />{{ i18nt('designer.toolbar.preview') }}</el-button>
        <el-button v-if="showToolButton('importJsonButton')" type="primary" link @click="importJson"> {{ i18nt('designer.toolbar.importJson') }}</el-button>
        <el-button v-if="showToolButton('exportJsonButton')" type="primary" link @click="exportJson"> {{ i18nt('designer.toolbar.exportJson') }}</el-button>
        <el-button v-if="showToolButton('exportCodeButton')" type="primary" link @click="exportCode"> {{ i18nt('designer.toolbar.exportCode') }}</el-button>
        <slot name="customToolButtons"></slot>
      </div>
    </div>

    <div v-if="showPreviewDialogFlag" class="" v-drag="['.drag-dialog.el-dialog', '.drag-dialog .el-dialog__header']">
      <el-dialog
        :title="i18nt('designer.toolbar.preview')"
        v-model="showPreviewDialogFlag"
        :show-close="true"
        :close-on-click-modal="false"
        :close-on-press-escape="false"
        center
        :destroy-on-close="true"
        :append-to-body="true"
        class="drag-dialog small-padding-dialog"
        width="75%"
        :fullscreen="layoutType === 'H5' || layoutType === 'Pad'"
      >
        <div>
          <div class="form-render-wrapper" :class="[layoutType === 'H5' ? 'h5-layout' : layoutType === 'Pad' ? 'pad-layout' : '']">
            <VmFormRender
              ref="preForm"
              :form-json="formJson"
              :form-data="testFormData"
              :preview-state="true"
              :option-data="testOptionData"
              :global-dsv="designerDsv"
              @appendButtonClick="testOnAppendButtonClick"
              @buttonClick="testOnButtonClick"
              @dataTableSelectionChange="testDTSC"
              @operationButtonClick="testOBC"
              @myEmitTest="onMyEmitTest"
              @formChange="handleFormChange"
            >
              <template v-for="(idx, slotName) in $slots" v-slot:[slotName]="scope">
                <slot :name="slotName" v-bind="scope"></slot>
              </template>
            </VmFormRender>
          </div>
        </div>
        <template #footer>
          <div class="dialog-footer">
            <el-button type="primary" @click="getFormData">{{ i18nt('designer.hint.getFormData') }}</el-button>
            <el-button type="primary" @click="resetForm">{{ i18nt('designer.hint.resetForm') }}</el-button>
            <el-button type="primary" @click="setFormDisabled">{{ i18nt('designer.hint.disableForm') }}</el-button>
            <el-button type="primary" @click="setFormEnabled">{{ i18nt('designer.hint.enableForm') }}</el-button>
            <el-button type="primary" plain @click="switchReadMode">{{ i18nt('designer.hint.switchReadMode') }}</el-button>
            <el-button @click="showPreviewDialogFlag = false">{{ i18nt('designer.hint.closePreview') }}</el-button>
            <el-button v-if="false" @click="testSetFormJson">setFormJson</el-button>
            <el-button v-if="false" @click="testSubFormHide">Test SFH</el-button>
            <el-button v-if="false" @click="testSetFormData">Test SFD</el-button>
          </div>
        </template>
      </el-dialog>
    </div>

    <div v-if="showImportJsonDialogFlag" class="" v-drag="['.drag-dialog.el-dialog', '.drag-dialog .el-dialog__header']">
      <el-dialog
        :title="i18nt('designer.toolbar.importJson')"
        v-model="showImportJsonDialogFlag"
        :show-close="true"
        class="drag-dialog small-padding-dialog"
        :append-to-body="true"
        center
        :close-on-click-modal="false"
        :close-on-press-escape="false"
        :destroy-on-close="true"
      >
        <el-alert type="info" :title="i18nt('designer.hint.importJsonHint')" show-icon class="alert-padding"></el-alert>
        <code-editor :mode="'json'" :readonly="false" v-model="importTemplate"></code-editor>
        <template #footer>
          <div class="dialog-footer">
            <el-button type="primary" @click="doJsonImport"> {{ i18nt('designer.hint.import') }}</el-button>
            <el-button @click="showImportJsonDialogFlag = false"> {{ i18nt('designer.hint.cancel') }}</el-button>
          </div>
        </template>
      </el-dialog>
    </div>

    <div v-if="showExportJsonDialogFlag" class="" v-drag="['.drag-dialog.el-dialog', '.drag-dialog .el-dialog__header']">
      <el-dialog
        :title="i18nt('designer.toolbar.exportJson')"
        v-model="showExportJsonDialogFlag"
        :show-close="true"
        class="drag-dialog small-padding-dialog"
        center
        append-to-body
        :close-on-click-modal="false"
        :close-on-press-escape="false"
        :destroy-on-close="true"
      >
        <code-editor :mode="'json'" :readonly="true" v-model="jsonContent"></code-editor>
        <template #footer>
          <div class="dialog-footer">
            <el-button type="primary" class="copy-json-btn" :data-clipboard-text="jsonRawContent" @click="copyFormJson"> {{ i18nt('designer.hint.copyJson') }}</el-button>
            <el-button @click="saveFormJson">{{ i18nt('designer.hint.saveFormJson') }}</el-button>
            <el-button @click="showExportJsonDialogFlag = false"> {{ i18nt('designer.hint.closePreview') }}</el-button>
          </div>
        </template>
      </el-dialog>
    </div>

    <div v-if="showExportCodeDialogFlag" class="" v-drag="['.drag-dialog.el-dialog', '.drag-dialog .el-dialog__header']">
      <el-dialog
        :title="i18nt('designer.toolbar.exportCode')"
        v-model="showExportCodeDialogFlag"
        :show-close="true"
        class="drag-dialog small-padding-dialog"
        center
        append-to-body
        width="65%"
        :close-on-click-modal="false"
        :close-on-press-escape="false"
        :destroy-on-close="true"
      >
        <el-tabs type="border-card" class="no-box-shadow no-padding" v-model="activeCodeTab">
          <el-tab-pane label="Vue" name="vue">
            <code-editor :mode="'html'" :readonly="true" v-model="vueCode" :user-worker="false"></code-editor>
          </el-tab-pane>
          <el-tab-pane label="HTML" name="html">
            <code-editor :mode="'html'" :readonly="true" v-model="htmlCode" :user-worker="false"></code-editor>
          </el-tab-pane>
        </el-tabs>
        <template #footer>
          <div class="dialog-footer">
            <el-button type="primary" class="copy-vue-btn" :data-clipboard-text="vueCode" @click="copyVueCode"> {{ i18nt('designer.hint.copyVueCode') }}</el-button>
            <el-button type="primary" class="copy-html-btn" :data-clipboard-text="htmlCode" @click="copyHtmlCode"> {{ i18nt('designer.hint.copyHtmlCode') }}</el-button>
            <el-button @click="saveVueCode">{{ i18nt('designer.hint.saveVueCode') }}</el-button>
            <el-button @click="saveHtmlCode">{{ i18nt('designer.hint.saveHtmlCode') }}</el-button>
            <el-button @click="showExportCodeDialogFlag = false"> {{ i18nt('designer.hint.closePreview') }}</el-button>
          </div>
        </template>
      </el-dialog>
    </div>

    <div v-if="showFormDataDialogFlag" class="" v-drag="['.nested-drag-dialog.el-dialog', '.nested-drag-dialog .el-dialog__header']">
      <el-dialog
        :title="i18nt('designer.hint.exportFormData')"
        v-model="showFormDataDialogFlag"
        :show-close="true"
        class="nested-drag-dialog dialog-title-light-bg"
        center
        :close-on-click-modal="false"
        :close-on-press-escape="false"
        :destroy-on-close="true"
        :append-to-body="true"
      >
        <div style="border: 1px solid #dcdfe6">
          <code-editor :mode="'json'" :readonly="true" v-model="formDataJson"></code-editor>
        </div>
        <template #footer>
          <div class="dialog-footer">
            <el-button type="primary" class="copy-form-data-json-btn" :data-clipboard-text="formDataRawJson" @click="copyFormDataJson"> {{ i18nt('designer.hint.copyFormData') }}</el-button>
            <el-button @click="saveFormData">{{ i18nt('designer.hint.saveFormData') }}</el-button>
            <el-button @click="showFormDataDialogFlag = false"> {{ i18nt('designer.hint.closePreview') }}</el-button>
          </div>
        </template>
      </el-dialog>
    </div>

    <div v-if="showExportSFCDialogFlag" class="" v-drag="['.drag-dialog.el-dialog', '.drag-dialog .el-dialog__header']">
      <el-dialog
        :title="i18nt('designer.toolbar.generateSFC')"
        v-model="showExportSFCDialogFlag"
        append-to-body
        v-if="showExportSFCDialogFlag"
        :show-close="true"
        class="drag-dialog small-padding-dialog"
        center
        width="65%"
        :close-on-click-modal="false"
        :close-on-press-escape="false"
        :destroy-on-close="true"
      >
        <el-tabs type="border-card" class="no-box-shadow no-padding" v-model="activeSFCTab">
          <el-tab-pane label="Vue2" name="vue2">
            <code-editor :mode="'html'" :readonly="true" v-model="sfcCode" :user-worker="false"></code-editor>
          </el-tab-pane>
          <el-tab-pane label="Vue3" name="vue3">
            <code-editor :mode="'html'" :readonly="true" v-model="sfcCodeV3" :user-worker="false"></code-editor>
          </el-tab-pane>
        </el-tabs>
        <template #footer>
          <div class="dialog-footer">
            <el-button type="primary" class="copy-vue2-sfc-btn" :data-clipboard-text="sfcCode" @click="copyV2SFC"> {{ i18nt('designer.hint.copyVue2SFC') }}</el-button>
            <el-button type="primary" class="copy-vue3-sfc-btn" :data-clipboard-text="sfcCodeV3" @click="copyV3SFC"> {{ i18nt('designer.hint.copyVue3SFC') }}</el-button>
            <el-button @click="saveV2SFC">{{ i18nt('designer.hint.saveVue2SFC') }}</el-button>
            <el-button @click="saveV3SFC">{{ i18nt('designer.hint.saveVue3SFC') }}</el-button>
            <el-button @click="showExportSFCDialogFlag = false"> {{ i18nt('designer.hint.closePreview') }}</el-button>
          </div>
        </template>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { getCurrentInstance, nextTick, ref, toRefs, reactive, inject, computed, onMounted } from 'vue';

import VmFormRender from '@/components/form-render/index';
import CodeEditor from '@/components/code-editor/index';
import SvgIcon from '@/components/svg-icon';
import Clipboard from 'clipboard';
import { deepClone, copyToClipboard, generateId, getQueryParam, traverseAllWidgets, addWindowResizeHandler } from '@/utils/util';
import { useI18n } from '@/utils/i18n';
import { generateCode } from '@/utils/code-generator';
import { genSFC } from '@/utils/sfc-generator';
import loadBeautifier from '@/utils/beautifierLoader';
import { saveAs } from 'file-saver';

export default {
  components: {
    Clipboard,
    CodeEditor,
    SvgIcon,
    VmFormRender,
  },
  name: 'ToolbarPanel',
  props: {
    designer: Object,
    globalDsv: {
      default: () => ({}),
      type: Object,
    },
  },
  setup(props) {
    const { i18nt } = useI18n();

    const { proxy } = getCurrentInstance();
    let $current = proxy;

    const getDesignerConfig = inject('getDesignerConfig');

    /*  */
    const nodeTree = ref(null);
    const preForm = ref(null);

    const data = reactive({
      activeCodeTab: 'vue',

      activeSFCTab: 'vue2',
      designerConfig: getDesignerConfig(),
      formDataJson: '',
      formDataRawJson: '',
      formReadonlyFlag: false,
      htmlCode: '',
      importTemplate: '',
      jsonContent: '',

      jsonRawContent: '',
      nodeTreeData: [],

      sfcCode: '',
      sfcCodeV3: '',
      showExportCodeDialogFlag: false,

      showExportJsonDialogFlag: false,
      showExportSFCDialogFlag: false,

      showFormDataDialogFlag: false,
      showImportJsonDialogFlag: false,

      showNodeTreeDrawerFlag: false,
      showPreviewDialogFlag: false,

      testFormData: {
        // 'userName': '666888',
        // 'productItems': [
        //   {'pName': 'iPhone12', 'pNum': 10},
        //   {'pName': 'P50', 'pNum': 16},
        // ]

        select62173: 2,
      },
      testOptionData: {
        // 'select62173': [
        //   {label: '01', value: 1},
        //   {label: '22', value: 2},
        //   {label: '333', value: 3},
        // ]
      },

      toolbarWidth: 420,
      vueCode: '',
    });

    onMounted(() => {});

    const formJson = computed(() => {
      return {
        formConfig: deepClone(props.designer.formConfig),
        widgetList: deepClone(props.designer.widgetList),
      };
    });

    const undoDisabled = computed(() => {
      return !props.designer.undoEnabled();
    });

    const redoDisabled = computed(() => {
      return !props.designer.redoEnabled();
    });

    const layoutType = computed(() => {
      return props.designer.getLayoutType();
    });

    const designerDsv = computed(() => {
      return props.globalDsv;
    });

    const showToolButton = configName => {
      if (data.designerConfig[configName] === undefined) {
        return true;
      }
      return !!data.designerConfig[configName];
    };

    const buildTreeNodeOfWidget = (widget, treeNode) => {
      let curNode = {
        id: widget.id,
        label: widget.options.label || widget.type,
        // selectable: true,
      };
      treeNode.push(curNode);

      if (widget.category === undefined) {
        return;
      }

      curNode.children = [];
      if (widget.type === 'm-grid') {
        widget.cols.map(col => {
          let colNode = {
            children: [],
            id: col.id,
            label: col.options.name || widget.type,
          };
          curNode.children.push(colNode);
          col.widgetList.map(wChild => {
            buildTreeNodeOfWidget(wChild, colNode.children);
          });
        });
      } else if (widget.type === 'm-table') {
        // TODO: 
        widget.rows.map(row => {
          let rowNode = {
            children: [],
            id: row.id,
            label: 'table-row',
            selectable: false,
          };
          curNode.children.push(rowNode);

          row.cols.map(cell => {
            if (cell.merged) {
              // 
              return;
            }

            let rowChildren = rowNode.children;
            let cellNode = {
              children: [],
              id: cell.id,
              label: 'm-table-cell',
            };
            rowChildren.push(cellNode);

            cell.widgetList.map(wChild => {
              buildTreeNodeOfWidget(wChild, cellNode.children);
            });
          });
        });
      } else if (widget.type === 'm-tab') {
        widget.tabs.map(tab => {
          let tabNode = {
            children: [],
            id: tab.id,
            label: tab.options.name || widget.type,
            selectable: false,
          };
          curNode.children.push(tabNode);
          tab.widgetList.map(wChild => {
            buildTreeNodeOfWidget(wChild, tabNode.children);
          });
        });
      } else if (widget.type === 'm-sub-form') {
        widget.widgetList.map(wChild => {
          buildTreeNodeOfWidget(wChild, curNode.children);
        });
      } else if (widget.category === 'container') {
        // 
        widget.widgetList.map(wChild => {
          buildTreeNodeOfWidget(wChild, curNode.children);
        });
      }
    };

    const refreshNodeTree = () => {
      data.nodeTreeData.length = 0;
      props.designer.widgetList.forEach(wItem => {
        buildTreeNodeOfWidget(wItem, data.nodeTreeData);
      });
    };

    const showNodeTreeDrawer = () => {
      refreshNodeTree();
      data.showNodeTreeDrawerFlag = true;
      nextTick(() => {
        if (props.designer.selectedId) {
          // 
          nodeTree.value.setCurrentKey(props.designer.selectedId);
        }
      });
    };

    const undoHistory = () => {
      props.designer.undoHistoryStep();
    };

    const redoHistory = () => {
      props.designer.redoHistoryStep();
    };

    const changeLayoutType = newType => {
      props.designer.changeLayoutType(newType);
    };

    const clearFormWidget = () => {
      props.designer.clearDesigner();
    };

    const previewForm = () => {
      formJson.value.widgetList = deepClone(props.designer.widgetList);
      formJson.value.formConfig = deepClone(props.designer.formConfig);

      data.showPreviewDialogFlag = true;
    };

    const saveAsFile = (fileContent, defaultFileName) => {
      $current
        .$prompt(i18nt('designer.hint.fileNameForSave'), i18nt('designer.hint.saveFileTitle'), {
          closeOnClickModal: false,
          inputPlaceholder: i18nt('designer.hint.fileNameInputPlaceholder'),
          inputValue: defaultFileName,
        })
        .then(({ value }) => {
          if (!value) {
            value = defaultFileName;
          }

          if (getQueryParam('vscode') === 1) {
            vsSaveFile(value, fileContent);
            return;
          }

          const fileBlob = new Blob([fileContent], { type: 'text/plain;charset=utf-8' });
          saveAs(fileBlob, value);
        })
        .catch(() => {
          //
        });
    };

    const vsSaveFile = (fileName, fileContent) => {
      const msgObj = {
        cmd: 'writeFile',
        data: {
          code: fileContent,
          fileName,
        },
      };
      window.parent.postMessage(msgObj, '*');
    };

    const importJson = () => {
      data.importTemplate = JSON.stringify(props.designer.getImportTemplate(), null, '  ');
      data.showImportJsonDialogFlag = true;
    };

    const doJsonImport = () => {
      try {
        let importObj = JSON.parse(data.importTemplate);
        // console.log('test import', this.importTemplate)
        if (!importObj || !importObj.formConfig) {
          throw new Error(i18nt('designer.hint.invalidJsonFormat'));
        }

        let fJsonVer = importObj.formConfig.jsonVersion;
        if (!fJsonVer || (fJsonVer !== -3 && fJsonVer !== -2)) {
          throw new Error(i18nt('designer.hint.jsonVersionMismatch'));
        }

        props.designer.loadFormJson(importObj);

        data.showImportJsonDialogFlag = false;
        $current.$message.success(i18nt('designer.hint.importJsonSuccess'));

        props.designer.emitHistoryChange();

        props.designer.emitEvent('form-json-imported', []);
      } catch (ex) {
        $current.$message.error(ex + '');
      }
    };

    const exportJson = () => {
      let widgetList = deepClone(props.designer.widgetList);
      let formConfig = deepClone(props.designer.formConfig);
      data.jsonContent = JSON.stringify({ formConfig, widgetList }, null, '  ');
      data.jsonRawContent = JSON.stringify({ formConfig, widgetList });
      data.showExportJsonDialogFlag = true;
    };

    const copyFormJson = e => {
      copyToClipboard(data.jsonRawContent, e, $current.$message, i18nt('designer.hint.copyJsonSuccess'), i18nt('designer.hint.copyJsonFail'));
    };

    const saveFormJson = () => {
      saveAsFile(data.jsonContent, `vform${generateId()}.json`);
    };

    const exportCode = () => {
      data.vueCode = generateCode(formJson.value);
      data.htmlCode = generateCode(formJson.value, 'html');
      data.showExportCodeDialogFlag = true;
    };

    const copyVueCode = e => {
      copyToClipboard(data.vueCode, e, $current.$message, i18nt('designer.hint.copyVueCodeSuccess'), i18nt('designer.hint.copyVueCodeFail'));
    };

    const copyHtmlCode = e => {
      copyToClipboard(data.htmlCode, e, $current.$message, i18nt('designer.hint.copyHtmlCodeSuccess'), i18nt('designer.hint.copyHtmlCodeFail'));
    };

    const saveVueCode = () => {
      saveAsFile(data.vueCode, `vform${generateId()}.vue`);
    };

    const saveHtmlCode = () => {
      saveAsFile(data.htmlCode, `vform${generateId()}.html`);
    };

    const generateSFC = () => {
      loadBeautifier(beautifier => {
        data.sfcCode = genSFC(props.designer.formConfig, props.designer.widgetList, beautifier);
        data.sfcCodeV3 = genSFC(props.designer.formConfig, props.designer.widgetList, beautifier, true);
        data.showExportSFCDialogFlag = true;
      });
    };

    const copyV2SFC = e => {
      copyToClipboard(data.sfcCode, e, $current.$message, i18nt('designer.hint.copySFCSuccess'), i18nt('designer.hint.copySFCFail'));
    };

    const copyV3SFC = e => {
      copyToClipboard(data.sfcCodeV3, e, $current.$message, i18nt('designer.hint.copySFCSuccess'), i18nt('designer.hint.copySFCFail'));
    };

    const saveV2SFC = () => {
      saveAsFile(data.sfcCode, `vformV2-${generateId()}.vue`);
    };

    const saveV3SFC = () => {
      saveAsFile(data.sfcCodeV3, `vformV3-${generateId()}.vue`);
    };

    const getFormData = () => {
      preForm.value
        .getFormData()
        .then(formData => {
          data.formDataJson = JSON.stringify(formData, null, '  ');
          data.formDataRawJson = JSON.stringify(formData);

          data.showFormDataDialogFlag = true;
        })
        .catch(error => {
          console.log(error);
          $current.$message.error(error);
        });
    };

    const copyFormDataJson = e => {
      copyToClipboard(data.formDataRawJson, e, $current.$message, i18nt('designer.hint.copyJsonSuccess'), i18nt('designer.hint.copyJsonFail'));
    };

    const saveFormData = () => {
      saveAsFile(data.formDataJson, `formData${generateId()}.json`);
    };

    const resetForm = () => {
      preForm.value.resetForm();
    };

    const setFormDisabled = () => {
      preForm.value.disableForm();
    };

    const setFormEnabled = () => {
      preForm.value.enableForm();
    };

    const switchReadMode = () => {
      data.formReadonlyFlag = !data.formReadonlyFlag;
      preForm.value.setReadMode(data.formReadonlyFlag);
