import axios from 'axios'

import VmFormDesigner from '@/components/form-designer/index.vue'
import VmFormRender from '@/components/form-render/index.vue'

import Draggable from '@/../lib/vuedraggable/dist/vuedraggable.umd.js'
import {registerIcon} from '@/utils/el-icons'
import 'virtual:svg-icons-register'
import '@/iconfont/iconfont.css'

import ContainerWidgets from '@/components/form-designer/form-widget/container-widget/index'
import ContainerItems from '@/components/form-render/container-item/index'

import { addDirective } from '@/utils/directive'
import { installI18n } from '@/utils/i18n'
import { installMessage } from '@/utils/message';
import { loadExtension } from '@/extension/extension-loader'


const setProductInfo = (app, options) => {
  app.config.globalProperties.$vformInfo = {
    productName: (options && options.productName) || '',
    productTitle: (options && options.productTitle) || ''
  }
}

VmFormDesigner.install = function (app,options={}) {
  addDirective(app)
  installI18n(app)
  installMessage(app);
  loadExtension(app)

  app.use(ContainerWidgets)
  app.use(ContainerItems)

  setProductInfo(app, options)

  registerIcon(app)
  app.component('draggable', Draggable)
  app.component(VmFormDesigner.name, VmFormDesigner)
}

VmFormRender.install = function (app) {
  installI18n(app)
  loadExtension(app)
  installMessage(app);

  app.use(ContainerItems)

  registerIcon(app)
  app.component(VmFormRender.name, VmFormRender)
}

const components = [
  VmFormDesigner,
  VmFormRender
]

const install = (app,options={}) => {
  addDirective(app)
  installI18n(app)
  installMessage(app);
  loadExtension(app)

  app.use(ContainerWidgets)
  app.use(ContainerItems)  
  
  setProductInfo(app, options)

  registerIcon(app)
  app.component('draggable', Draggable)

  components.forEach(component => {
    app.component(component.name, component)
  })

  window.axios = axios
}

if (typeof window !== 'undefined' && window.Vue) { /* scriptaxios */
  //window.axios = axios
}

export default {
  install,
  VmFormDesigner,
  VmFormRender
}
