<template>
  <el-form-item :label="i18nt('designer.setting.customClass')">
    <el-select v-model="optionModel.customClass" multiple filterable allow-create default-first-option>
      <el-option v-for="(item, idx) in cssClassList" :key="idx" :label="item" :value="item"></el-option>
    </el-select>
  </el-form-item>
</template>

<script>
import { reactive, toRefs } from 'vue';
import { useI18n } from '@/utils/i18n';
import { deepClone } from '@/utils/util';
import { useProperty } from '@/components/form-designer/setting-panel/property-editor/propertyMixin';

export default {
  componentName: 'PropertyEditor',
  name: 'customClass-editor',
  props: {
    designer: Object,
    optionModel: Object,
    selectedWidget: Object,
  },
  setup(props) {
    const { i18nt } = useI18n();
    const properyMixins = useProperty();
    const data = reactive({
      cssClassList: [],
    });

    data.cssClassList = deepClone(props.designer.getCssClassList());
    // css
    props.designer.handleEvent('form-css-updated', cssClassList => {
      data.cssClassList = cssClassList;
    });

    return {
      i18nt,
      ...toRefs(props),
      ...toRefs(data),
    };
  },
};
</script>

<style scoped></style>
