<template>
  <div class="side-scroll-bar">
    <el-collapse v-model="activeNames" class="widget-collapse">
      <el-collapse-item name="1" :title="i18nt('designer.containerTitle')" class="collapse-item">
        <draggable
          tag="ul"
          :list="containers"
          item-key="id"
          :group="{ name: 'dragGroup', pull: 'clone', put: false }"
          class="field-widget-container"
          :clone="handleContainerWidgetClone"
          ghost-class="ghost"
          :sort="false"
          :move="checkContainerMove"
          @end="onContainerDragEnd"
        >
          <template #item="{ element: ctn }">
            <li class="container-widget-item" :title="ctn.displayName" @dblclick="addContainerByDbClick(ctn)">
              <span>
                <svg-icon :icon-class="ctn.icon" class-name="color-svg-icon" />
                <div class="container-widget-item-label">{{ i18n2t(`designer.widgetLabel.${ctn.type}`, `extension.widgetLabel.${ctn.type}`) }}</div>
              </span>
            </li>
          </template>
        </draggable>
      </el-collapse-item>

      <el-collapse-item name="2" :title="i18nt('designer.basicFieldTitle')">
        <draggable
          tag="ul"
          :list="basicFields"
          item-key="id"
          :group="{ name: 'dragGroup', pull: 'clone', put: false }"
          class="field-widget-container"
          :move="checkFieldMove"
          :clone="handleFieldWidgetClone"
          ghost-class="ghost"
          :sort="false"
        >
          <template #item="{ element: fld }">
            <li class="field-widget-item" :title="fld.displayName" @dblclick="addFieldByDbClick(fld)">
              <span>
                <svg-icon :icon-class="fld.icon" class-name="color-svg-icon" />
                <div class="container-widget-item-label">{{ i18n2t(`designer.widgetLabel.${fld.type}`, `extension.widgetLabel.${fld.type}`) }}</div>
              </span>
            </li>
          </template>
        </draggable>
      </el-collapse-item>

      <el-collapse-item name="3" :title="i18nt('designer.advancedFieldTitle')">
        <draggable
          tag="ul"
          :list="advancedFields"
          item-key="id"
          :group="{ name: 'dragGroup', pull: 'clone', put: false }"
          :move="checkFieldMove"
          :clone="handleFieldWidgetClone"
          ghost-class="ghost"
          :sort="false"
        >
          <template #item="{ element: fld }">
            <li class="field-widget-item" :title="fld.displayName" @dblclick="addFieldByDbClick(fld)">
              <span>
                <svg-icon :icon-class="fld.icon" class-name="color-svg-icon" />
                <div class="container-widget-item-label">{{ i18n2t(`designer.widgetLabel.${fld.type}`, `extension.widgetLabel.${fld.type}`) }}</div>
              </span>
            </li>
          </template>
        </draggable>
      </el-collapse-item>

      <el-collapse-item name="4" :title="i18nt('designer.customFieldTitle')">
        <draggable
          tag="ul"
          :list="customFields"
          item-key="id"
          :group="{ name: 'dragGroup', pull: 'clone', put: false }"
          :move="checkFieldMove"
          :clone="handleFieldWidgetClone"
          ghost-class="ghost"
          :sort="false"
        >
          <template #item="{ element: fld }">
            <li class="field-widget-item" :title="fld.displayName" @dblclick="addFieldByDbClick(fld)">
              <span>
                <svg-icon :icon-class="fld.icon" class-name="color-svg-icon" />
                <div class="container-widget-item-label">{{ i18n2t(`designer.widgetLabel.${fld.type}`, `extension.widgetLabel.${fld.type}`) }}</div>
              </span>
            </li>
          </template>
        </draggable>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>
<script>
import SvgIcon from '@/components/svg-icon';
import { containers, basicFields, advancedFields, customFields } from '../widgetsConfig';
// import Draggable from 'vuedraggable';
import { useI18n } from '@/utils/i18n';
import { reactive, toRefs, inject, onMounted } from 'vue';
export default {
  components: {
    SvgIcon,
    // Draggable,
  },
  icon: 'grid',
  label: 'toolbox.widgets',
  name: 'widgets',
  setup(props) {
    const designer = inject('designer');
    const getBannedWidgets = inject('getBannedWidgets');
    const data = reactive({
      activeNames: ['1', '2', '3', '4'],
      advancedFields,
      basicFields,
      containers,
      customFields,
      label: 'toolbox.widgets',
    });

    const { i18nt, i18n2t } = useI18n();

    const isBanned = wName => {
      return getBannedWidgets().indexOf(wName) > -1;
    };
    const loadWidgets = () => {
      data.containers = containers
        .map(con => {
          return {
            ...con,
            displayName: i18n2t(`designer.widgetLabel.${con.type}`, `extension.widgetLabel.${con.type}`),
          };
        })
        .filter(con => {
          return !con.internal && !isBanned(con.type);
        });

      data.basicFields = basicFields
        .map(fld => {
          return {
            ...fld,
            displayName: i18n2t(`designer.widgetLabel.${fld.type}`, `extension.widgetLabel.${fld.type}`),
          };
        })
        .filter(fld => {
          return !isBanned(fld.type);
        });

      data.advancedFields = advancedFields
        .map(fld => {
          return {
            ...fld,
            displayName: i18n2t(`designer.widgetLabel.${fld.type}`, `extension.widgetLabel.${fld.type}`),
          };
        })
        .filter(fld => {
          return !isBanned(fld.type);
        });

      data.customFields = customFields
        .map(fld => {
          return {
            ...fld,
            displayName: i18n2t(`designer.widgetLabel.${fld.type}`, `extension.widgetLabel.${fld.type}`),
          };
        })
        .filter(fld => {
          return !isBanned(fld.type);
        });

      if (import.meta.env.VITE_APP_ENV_STANDARD) {
        let deleteFields = ['m-sub-form', 'm-slot'];
        data.containers = containers.filter(x => {
          return (
            deleteFields.findIndex(f => {
              return f == x.type;
            }) == -1
          );
        });
        data.basicFields = basicFields.filter(x => {
          return (
            deleteFields.findIndex(f => {
              return f == x.type;
            }) == -1
          );
        });
        data.advancedFields = advancedFields.filter(x => {
          return (
            deleteFields.findIndex(f => {
              return f == x.type;
            }) == -1
          );
        });
      }
    };

    const handleContainerWidgetClone = origin => {
      return designer.copyNewContainerWidget(origin);
    };

    const handleFieldWidgetClone = origin => {
      return designer.copyNewFieldWidget(origin);
    };

    const checkContainerMove = evt => {
      return designer.checkWidgetMove(evt);
    };

    const checkFieldMove = evt => {
      return designer.checkFieldMove(evt);
    };

    const onContainerDragEnd = () => {};

    const addContainerByDbClick = container => {
      designer.addContainerByDbClick(container);
    };

    const addFieldByDbClick = widget => {
      designer.addFieldByDbClick(widget);
    };

    onMounted(() => {
      loadWidgets();
    });

    return {
      ...toRefs(data),
      addContainerByDbClick,
      addFieldByDbClick,
      checkContainerMove,
      checkFieldMove,
      handleContainerWidgetClone,
      handleFieldWidgetClone,
      i18n2t,
      i18nt,
      onContainerDragEnd,
    };
  },
  sort: 0,
};
</script>
